body {
  background-color: rgba(0, 0, 0, 0.0175)
}

accordion .panel-heading {
  padding: 0;

  a.accordion-toggle {
    width: 100%;
    display: block;
    padding: 10px;
    margin: 0;
    text-decoration: none;
  }

  a.accordion-toggle:hover {
    background-color: rgba(0, 0, 0, 0.05);
  }
}

accordion .panel-group .panel + .panel, accordion .panel-group .panel {
  margin-top: 0;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0;
}